.algorithm {
  background: linear-gradient(to bottom, #130d2c, #100c23);
  position: relative;
  // overflow-x: hidden;

  &__container {
    overflow: hidden;
    padding: 50px 15px 70px;
    position: relative;
    display: grid;
    column-gap: 40px;
    grid-template-columns: 1fr 50%;
    grid-template-areas:
      'text image1'
      'text image2';

    &::before {
      content: '';
      position: absolute;
      right: 192px;
      top: 50%;
      width: 389px;
      height: 389px;
      transform: translateY(-50%);
      background: #dd00b8;
      border-radius: 50%;
      filter: blur(200px);
      opacity: 0.5;
    }
    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 774px;
      height: 100%;
      background: url('../img/new-home-page/algorithm/vector.svg') no-repeat 0 0 / cover;
      opacity: 0.8;
    }
  }

  &__text {
    grid-area: text;
    position: relative;
    z-index: 1;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: -170px;
      transform: translateY(-50%);
      width: 241px;
      height: 241px;
      background-color: #dd00b8;
      border-radius: 50%;
      filter: blur(200px);
      opacity: 0.5;
    }
  }
  &__item--tablet-bg {
    display: none;
  }
  &__item--1 {
    grid-area: image1;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      right: -60%;
      top: -10%;
      bottom: 53%;
      width: 400px;
      background: url('../img/new-home-page/algorithm/plane.svg') no-repeat 0 0 / cover;
    }
  }

  &__item--2 {
    grid-area: image2;
  }

  &__title {
    margin-bottom: 20px;
    width: fit-content;
    font-family: $font-popins-semb;
    font-size: 40px;
    line-height: 130%;
    background: linear-gradient(254deg, #fff 48.55%, #e6466e 72.99%, #e548ca 97%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__sub-title {
    color: rgba(255, 255, 255, 0.8);
    font-family: $font-popins-reg;
    font-size: 16px;
    line-height: 150%;
  }
  &__lines {
    margin-top: 40px;
    margin-bottom: 34px;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 1px;
      width: 100%;
      background: linear-gradient(90deg, rgba(227, 44, 196, 0.8) 1.04%, rgba(227, 44, 196, 0) 100%);
    }
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      width: 100%;
      background: linear-gradient(90deg, rgba(227, 44, 196, 0.8) 1.04%, rgba(227, 44, 196, 0) 100%);
    }
  }
  &__line {
    position: relative;
    display: flex;
    gap: 14px;
    padding: 18px 0;
    color: rgba(255, 255, 255, 0.8);
    font-family: $font-popins-reg;
    font-size: 16px;
    line-height: 150%;
    &::before {
      content: '';
      display: block;
      position: relative;
      min-width: 40px;
      width: 40px;
      height: 40px;
      background: url('../img/new-home-page/algorithm/icon.svg') no-repeat 0 0 / cover;
    }
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      width: 100%;
      background: linear-gradient(90deg, rgba(227, 44, 196, 0.8) 1.04%, rgba(227, 44, 196, 0) 100%);
    }
  }
  &__link {
    margin-top: 24px;
    color: #e5053d;
    font-family: $font-popins-bold;
    font-size: 20px;
    letter-spacing: 0.4px;
    text-transform: uppercase;
    position: relative;
    transition: all $transition;
    display: block;

    &::after {
      content: '';
      display: inline-block;
      width: 18px;
      height: 13px;
      background-color: #e5053d;
      -webkit-mask: url('../img/new-page/header-menu-arrow.svg') no-repeat center;
      -webkit-mask-size: contain;
      mask: url('../img/new-page/header-menu-arrow.svg') no-repeat center;
      mask-size: contain;
      margin: 0 0 0 8px;
      transition: all $transition;
    }
    &:hover {
      color: #b4012e;
      &::after {
        background-color: #b4012e;
      }
    }
  }
  &__item {
    position: relative;
    width: 400px;
    height: 340px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    border: 1px solid #716c6cab;
    border-radius: 12px;
    img {
      position: relative;
      z-index: 1;
    }
    &::after {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: 12px;
      background-color: linear-gradient(180deg, rgba(255, 255, 255, 0.08) 0%, rgba(217, 217, 217, 0.04) 100%);
      backdrop-filter: blur(25px);
    }
    &--1 {
    }
    &--2 {
      margin-left: auto;
      top: -34px;
    }
  }
}

@media (max-width: 1150px) {
  .algorithm {
    &__item--1 {
      &::before {
        display: none;
      }
    }
  }
}
@media (max-width: 1050px) {
  .algorithm {
    &__container {
      row-gap: 15px;
      &::before {
        right: 0;
        opacity: 0.3;
      }
      &::after {
        display: none;
      }
    }
    &__title {
      font-size: 32px;
    }
    &__sub-title {
      font-size: 14px;
    }
    &__lines {
      margin-top: 30px;
      margin-bottom: 30px;
    }
    &__line {
      font-size: 14px;
      padding: 14px 0;
      &::before {
        width: 35px;
        height: 35px;
      }
    }
    &__link {
      font-size: 14px;
    }
    &__item {
      &--2 {
        top: 0;
      }
    }
  }
}

@media (max-width: 1030px) {
  .algorithm {
    &__container {
      grid-template-columns: 1fr auto;
    }
  }
}
@media (max-width: 900px) {
  .algorithm {
    &__item {
      width: 347px;
      height: 280px;
    }
  }
}
@media (max-width: 750px) {
  .algorithm {
    &__container {
      padding: 40px 15px;
      grid-template-columns: 1fr;
      row-gap: 30px;
      grid-template-areas:
        'image1'
        'text'
        'image2';
      &::before {
        display: none;
      }
    }
    &__text {
      &::before {
        display: none;
      }
    }
    &__item {
      margin: 0 auto;
      width: 400px;
      height: 340px;
      &--tablet-bg1 {
        display: block;
        position: absolute;
        width: 100%;
        height: 504px;
        &::before {
          content: '';
          position: absolute;
          width: 299px;
          height: 299px;
          background: #dd00b8;
          border-radius: 50%;
          filter: blur(100px);
          opacity: 0.4;
          inset: 0;
          top: 50%;
          transform: translateX(50px);
        }
        &::after {
          content: '';
          content: '';
          position: absolute;
          width: 750px;
          height: 750px;
          background: url('../img/new-home-page/algorithm/vector.svg') no-repeat 0 0 / cover;
          opacity: 0.6;
          inset: 0;
          left: -175px;
          bottom: auto;
          top: -150px;
        }
      }
      &--tablet-bg2 {
        display: block;
        position: absolute;
        width: 100%;
        height: 504px;
        &::before {
          content: '';
          position: absolute;
          width: 299px;
          height: 299px;
          background: #dd00b8;
          border-radius: 50%;
          filter: blur(100px);
          opacity: 0.4;
          inset: 0;
          bottom: 50%;
          transform: translateX(50px);
        }
        &::after {
          content: '';
          content: '';
          position: absolute;
          width: 750px;
          height: 750px;
          background: url('../img/new-home-page/algorithm/vector.svg') no-repeat 0 0 / cover;
          opacity: 0.6;
          inset: 0;
          left: -175px;
          bottom: auto;
          top: -150px;
        }
      }
    }
  }
}
@media (max-width: 560px) {
  .algorithm {
    &__link {
      font-size: 14px;
      &::after {
        width: 14px;
        height: 10px;
      }
    }
  }
}
@media (max-width: 490px) {
  .algorithm {
    &__title {
      font-size: 24px;
    }
    &__sub-title {
      font-size: 12px;
    }
    &__lines {
      margin-top: 20px;
      margin-bottom: 20px;
    }
    &__line {
      font-size: 12px;
      padding: 12px 0;
      &::before {
        width: 30px;
        height: 30px;
      }
    }
  }
}

@media (max-width: 450px) {
  .algorithm {
    &__item {
      width: 347px;
      height: 280px;
      img {
        width: 210px;
        height: 210px;
      }
      &--tablet-bg2,
      &--tablet-bg1 {
        &::before {
          transform: translateX(22px);
        }
        &::after {
          left: -202px;
        }
      }
    }
  }
}

